@import url(https://fonts.googleapis.com/css?family=ABeeZee);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'ABeeZee', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: #333;
}

.nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  height: 70px;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 6px 0 rgba(0, 0, 0, 0.2); }
  .nav .logo {
    font-size: 1.2rem; }
  .nav .nav-item-list {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    list-style-type: none;
    padding: 0; }
  .nav .nav-item {
    padding: 0 1rem; }

.button {
  background: #fff;
  width: 100px;
  height: 40px;
  font-size: 14px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  outline: none;
  margin: 15px auto;
  cursor: pointer; }
  .button:hover {
    opacity: 0.7; }

.avatar-card {
  box-shadow: 0px 0px 6px 0 rgba(0, 0, 0, 0.2);
  width: 300px;
  height: auto;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }
  .avatar-card .name {
    font-size: 18px;
    text-align: center; }
  .avatar-card .card-image {
    border-radius: 50%;
    width: 80%;
    height: auto;
    padding: 10px 0;
    margin: 0 auto; }
  .avatar-card .avatar-profile {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: wrap column;
            flex-flow: wrap column; }
  .avatar-card.spinner {
    box-shadow: none; }

.about {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 20px; }

.search-box {
  margin-bottom: 32px; }
  .search-box .search-box-input {
    font-size: 16px;
    padding: 10px 20px;
    border: 1px solid #808080; }
  .search-box p {
    font-size: 12px;
    color: rgba(51, 51, 51, 0.7); }

.card {
  width: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 6px 0 rgba(0, 0, 0, 0.2);
  padding: 20px;
  margin: 20px;
  cursor: pointer; }
  .card .card-profile {
    height: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end; }
    .card .card-profile .card-name {
      margin: 0;
      font-size: 16px; }
  .card .card-image {
    border-radius: 50%;
    width: 200px;
    height: 200px; }
  .card:hover {
    opacity: 0.7; }

.card-list {
  width: 100%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto; }

.scroll {
  overflow-y: scroll;
  height: 100vh;
  border: 1px dotted #ddd;
  padding: 0 20px;
  -webkit-overflow-scrolling: touch; }

.directory {
  text-align: center; }

